import { Badge, Container, ListGroup, Nav, Navbar, Offcanvas } from "react-bootstrap"

export default function(props) {
    

    return (
        <>
            <Navbar sticky="top" bg="warning" data-bs-theme="dark" expand="md" className="bg-body-tertiary mb-3">
                <Container fluid>
                    <Navbar.Brand href="#">Работа с остатками</Navbar.Brand>
                    <Navbar.Toggle aria-controls="offcanvas-remains-nav" />

                    <Navbar.Offcanvas
                        id="offcanvas-remains-nav" 
                        aria-labelledby="offcanvas-remains-nav-label" 
                        placement="start">
                        
                        <Offcanvas.Header closeButton>
                            <Offcanvas.Title id={`offcanvas-remains-nav-label`}>
                                Работа с остатками
                            </Offcanvas.Title>
                            
                        </Offcanvas.Header>
                            <Offcanvas.Body>
                            <Nav>
                                <Nav.Link>Список остатков</Nav.Link>
                            </Nav>
                            </Offcanvas.Body>
                    </Navbar.Offcanvas>
                    
                </Container>
            </Navbar>
        </>
    );
}