import axios from "axios";
import { useEffect, useState } from "react";
import { Button, ButtonGroup, InputGroup, Form, Modal, Container, Col, Row, Spinner, Alert } from "react-bootstrap";
import Config from "../../../config.json";


export default function({show, onSubmit, onCancel, AutoloadData, ...props}) {

    const [name,setName] = useState("");
    const [sizeX,setSizeX] = useState("");
    const [sizeY,setSizeY] = useState("");
    const [founded,setLMFounded] = useState(false);
    const [lmCode,setLmCode] = useState("");
    const [writingMyself,setMyself] = useState(false);
    const [searching,setSearching] = useState(false);
    const [alertMessage,setAlertMessage] = useState({
        show: false,
        message: "",
        variant: "danger"
    });

    const [allowedSubmit,setAllowedSubimt] = useState(false);

    const proceedForm = (e) => {
        e.preventDefault();
        let item = {
            lm_code: lmCode,
            Name: name,
            sizeX: sizeX,
            sizeY: sizeY
        }
        onSubmit(item);
    }

    const AlertWrongLM = (message,variant="danger") => {
        setAlertMessage({
            show: true,
            message: message,
            variant: variant
        });
    }

    const clearForm = () => {
        console.log("Cleared");
        setName("");
        setSizeX("");
        setSizeY("");
        setLmCode("");
        setMyself(false);
        setLMFounded(false);
        setAlertMessage({
            show: false,
            message: ""
        });
    }

    const fillFormWithItem = (item) => {
        setAlertMessage({
            show: false,
            message: "",
            variant: "danger"
        });
        setLmCode(item.lm_code.toString());
        setName(item.item_name);
        setLMFounded(true);
        AlertWrongLM("Данный товар найден","success");
    };

    const searchByLM = () => {
        setMyself(false);
        setSearching(true);
        axios.get(
            `${Config.API_URL}/items/get?lm=${lmCode}`
        ).then((result) => {
            if(result.status == 404) {
                AlertWrongLM("Товар с данным LM не найден.");
            }
            else {
                fillFormWithItem(result.data);
            }
        }).catch((e) => {
                AlertWrongLM("Произошла внутренняя ошибка. Попробуйте позже.");
        }).finally(() => {
            setSearching(false);
        });
    };

    useEffect(() => {
        setAllowedSubimt(canSubmit);
    },[lmCode,sizeX,sizeY]);

    const canSubmit = () => {
        console.log(lmCode,lmCode.length);
        return (sizeX > 0 && sizeX <= 4) &&
        (sizeY > 0 && sizeY <= 5) &&
        name.length > 3 &&
        lmCode.length > 3
    }

    useEffect(() => {
        if(show == true) {
            clearForm();
            if(AutoloadData) {
                if(AutoloadData.needLoad) {
                    fillFormWithItem(AutoloadData.data);
                }
            }
            else {
                console.log("Autoload data not defined")
            }
        }
    },[show])

    return (
        <Modal show={show}>
            
            <Modal.Header>
                <Modal.Title>Добавление нового остатка</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Container>
                    <InputGroup>
                        <Form.Control
                            placeholder="Введите LM код для поиска" value={lmCode} type="number" onChange={({target: {value}}) => setLmCode(value)}/>
                        <Button variant="secondary" disabled={searching} onClick={searchByLM}>
                            { !searching ?
                               "Найти" :
                               <Spinner animation="border" size="sm"/>
                            }
                        </Button>
                    </InputGroup>
                    <Form.Check type="switch" checked={writingMyself} onChange={(e) => setMyself(e.target.checked)} label="Ввести данные самостоятельно" />
                    <Alert show={alertMessage.show} variant={alertMessage.variant}>{alertMessage.message}</Alert>
                    
                    <hr/>
                    <Form.Group>
                        <Form.Label>
                            Наименование линолеума:
                        </Form.Label>
                        <Form.Control type="text" disabled={!writingMyself} value={name} onChange={(e) => setName(e.target.value)}/>
                    </Form.Group>
                    <Row>
                        <Col>
                            <Form.Group>
                                <Form.Label>Ширина линолеума:</Form.Label>
                                <Form.Control type="number" disabled={!writingMyself && !founded} value={sizeX} onChange={(e) => setSizeX(e.target.value)} />
                                <Form.Text muted>В основном, ширина указаывается в названии</Form.Text>
                            </Form.Group>
                        </Col>
                        <Col>
                            <Form.Group>
                                <Form.Label>Длина линолеума:</Form.Label>
                                <Form.Control type="number" disabled={!writingMyself && !founded} value={sizeY} onChange={(e) => setSizeY(e.target.value)} />
                                <Form.Text muted>Длина отрезка</Form.Text>
                            </Form.Group>
                        </Col>
                    </Row>
                </Container>
            </Modal.Body>
            <Modal.Footer>
                <ButtonGroup>
                    <Button variant="outline-secondary" onClick={onCancel}>Отмена</Button>
                    <Button variant="outline-primary" onClick={proceedForm} disabled={!allowedSubmit}>Добавить</Button>
                </ButtonGroup>
            </Modal.Footer>
        </Modal>
    );

}