import RemainPage from './Pages/RemainsPage/RemainsPage';
import RemainsDashboard from './Pages/RemainsPage/Subpages/RemainsDashboard';
import WelcomeMessage from './Pages/RemainsPage/Components/WelcomeMessage';
import AddNewRemains from './Pages/RemainsPage/Subpages/AddNewRemains';
import AboutPage from './Pages/AboutPage';
import PrintingRemainsPage from './Pages/RemainsPage/Subpages/PrintingRemainsPage';

axios.interceptors.response.use((res) => {
  return res;
},(res) => {
  /*if(res.status == 401 && window.location.href != window.location.origin+"/auth") {
    window.location.href = window.location.origin+"/auth";
  }*/
  return res;
});

axios.interceptors.request.use((config) => {
  if(cookies.get('Autorization')) {
      config.headers.Authorization = cookies.get('Autorization');
  }
  return config;
});

function getAuth() {
    console.log(cookies.get('Autorization'));
}

const cookies = new Cookies();

function App() {
  const navigator = useNavigate();
  var User = null;

  const [alive,setAlive] = useState(true);



  useEffect(() => {
    axios.get(
      `${Config.API_URL}/manifest`
    ).then((response) => {
      if(response.status == 200) {
      if(cookies.get('Autorization')) {
        axios.post(
          `${Config.API_URL}/auth/check`,{},
          {
            headers: {
              "authorization": cookies.get('Autorization')
            }
          }
        ).then(({status, data }) => {
          if(status == 401) {
            navigator("/auth");
          }
          else {
            User = data;
          }
          console.log(data);
        }).catch(d => {
          console.log(d);
          navigator("/auth");
        });
      }
      else {
        navigator("/auth");
      }
      }
      else {
        setAlive(false);
      }
    }).catch((err) => {
        setAlive(false);
    });
  },[]);

  const [showBack,setBack] = useState(false);

 
  
  return (
  <>
      { alive ? 
      <Routes>
        <Route path="/" element={<MainPage />}/>
        <Route path="/remains" element={<RemainPage userData={User}/>}>
            <Route path="/remains" element={<WelcomeMessage />} />
            <Route path="/remains/list" element={<RemainsDashboard />} />
            <Route path="/remains/addRemains" element={<AddNewRemains />}/>
            <Route path="/remains/preparePrinting" element={<PrintingRemainsPage />}/>
            <Route path="*" element={<ErrorPage />}/>
        </Route>
        <Route path="/about" element={<AboutPage />}/>
        <Route path="/mixes" element={<ServiceDisabled showReturn showMenu/>}/>
        <Route path="/alleys" element={<ServiceDisabled showReturn showMenu/>}/>
        <Route path="/jobs" element={<ServiceDisabled showReturn showMenu/>}/>
        <Route path="/auth" element={<AuthPage />}/>
        <Route path="*" element={<ErrorPage showReturn/>}/>
      </Routes>
      : 
        <Container>
            <Row>
                <Col className='text-center'>
                  <h2>Нет соединения</h2>
                  <p>Соединение с основным сервером отсутствует. Попробуйте позже.</p>
                </Col>
            </Row>
        </Container>
      }
  </>
  );
}

export default App;
