import axios from "axios";
import { useEffect, useState } from "react";
import { Badge, Button, ButtonGroup, Card, Col, Row, Spinner } from "react-bootstrap";
import Config from "../../../config.json";


export default function({item, onAddPressed, ...props}) {

    const [addedToBD,addToBD] = useState(false);
    const [isLoading,setLoading] = useState(false);

    const addItem = () => {
        setLoading(true);
        axios.post(
            `${Config.API_URL}/items/add`,
            {
                item: item
            }
        ).then(res => {
            addToBD(true);
        }).catch(e => {
        }).finally(() => {
            setLoading(false);
        });
    }

    useEffect(() => {},[item]);

    return (
        <Card className="p-2 m-1">
            <Row>
                <Col>
                    <h6>{item.Name}</h6>
                </Col>
                <Col>
                    <h6 className="text-muted float-end">LM: {item.lm_code}</h6>
                </Col>
            </Row>
            <Row>
                <Col>Ширина:</Col>
                <Col>
                    <Badge bg="secondary">{item.sizeX}</Badge>
                </Col>
            </Row>
            <Row>
                <Col>Длина:</Col>
                <Col>
                    <Badge bg="secondary">{item.sizeY}</Badge>
                </Col>
            </Row>
            <Row>
                <Col>
                    <Card className="p-2 m-1">
                        <Card.Text>Выберите действия ниже, чтобы исправить</Card.Text>
                    </Card>
                </Col>
            </Row>
            <Row>
                <ButtonGroup>
                    { !isLoading ? 
                    <Button variant="outline-secondary" onClick={addItem} disabled={addedToBD}>
                        {!addedToBD ? "Добавить данный линолеум в базу" : "Добавлено в базу данных" }
                    </Button> :
                    <Button variant="outline-secondary" disabled={true}>
                        <Spinner size="sm"/> Добавяем...
                    </Button>
                    }
                </ButtonGroup>
            </Row>
        </Card>
    )

}