import { useState } from "react";
import { Alert, Badge, Button, Card, Col, Container, Form, Row } from "react-bootstrap";


export default function({Item, onPriceChanged, arrNum, ...props}) {
    
    const [alertSelect,setAlert] = useState(false);
    const [color,setColor] = useState('#cc5555');
    const index = arrNum;

    const onChangeHandler = (e) => {
        let price = e.target.value;
        if(price.toString().length == 0 || Number(price) <= 0) {
            setAlert(true);
            setColor('#cc5555');
        }
        else {
            setAlert(false);
            setColor('#55cc55');
        }
        onPriceChanged(arrNum,price);
    }

    return (
        <Card className="shadow mb-2">
            <Card.Header style={{
                background: `${color}`
            }}>
                <Card.Title>
                <span>{Item.Name} (<a className="text-muted" style={{textDecoration: 'none'}} href={`https://ekaterinburg.lemanapro.ru/search/?q=${Item.lm_code}`} target="_blank">{Item.lm_code}</a>)</span>
                </Card.Title>
            </Card.Header>
            {console.log(arrNum)}
            <Container fluid>
                <Row className="pt-2">
                    <Col className="">
                    Остатков: 
                    </Col>
                    <Col className="d-flex justify-content-end">
                    <Badge bg="secondary">{Item.List.length}</Badge>
                    </Col>
                </Row>
                <hr />
                <Row className="pb-2">
                    <Col sm={2}>
                        Цена за м2:
                    </Col>
                    <Col className="d-flex justify-content-end">
                        <Form.Control size="sm" type="number" onChange={onChangeHandler} placeholder="Цена, р" />
                    </Col>
                </Row>
            </Container>
        </Card>
    );
}