import { useEffect, useState } from "react";
import { Accordion, AccordionBody, Alert, Badge, Button, Card, Col, Modal, Row } from "react-bootstrap";
import FailedRemain from "./FailedRemain";


export default function({show, data, onClose, ...props}) {

    const [hasError,setError] = useState(false);

    useEffect(() => {
            setError(data.failed > 0)
    },[show,data])

    return (
        <Modal show={show}>
            <Modal.Header>
                <Modal.Title>Результат добавления</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Alert show={hasError} variant="danger">Во время добавления новых остатков произошла ошибка. Проверьте результаты</Alert>
                <Alert show={!hasError} variant="success">Все новые остатки были добавлены.</Alert>
                <Row>
                    <Col>Новых остатков:</Col>
                    <Col>
                        <Badge bg="success">{data.added}</Badge>
                    </Col>
                </Row>
                <Row>
                    <Col>Не было добавлено:</Col>
                    <Col>
                        {data.failed > 0 ? 
                        <Badge bg="danger">{data.failed}</Badge>  :
                        <Badge bg="success">{data.failed}</Badge>
                        }
                    </Col>
                </Row>
                {data.failed > 0 &&
                    <Row className="p-1">
                        <Col>
                        <Accordion>
                            <Accordion.Item eventKey="0">
                                <Accordion.Header variant="danger">Не было добавлено</Accordion.Header>
                                <AccordionBody>
                                    {data.failedItems.map(item => 
                                            <FailedRemain item={item} />
                                    )}
                                </AccordionBody>
                            </Accordion.Item>
                        </Accordion>
                        </Col>
                    </Row>
                }
            </Modal.Body>
            <Modal.Footer>
                <Button variant="primary" onClick={onClose}>OK</Button>
            </Modal.Footer>
        </Modal>
    );
}