import { useEffect, useState } from "react";
import { Alert, Badge, Button, Card, Modal } from "react-bootstrap";


export default function({onDelete, onCancel, remain, show, ...props}) {
    
    const [counter,setCounter] = useState(4);

    useEffect(() => {
        console.log(remain);
    },[remain])

    useEffect(() => {
        if(counter > 0) {
            setTimeout(() => {
                setCounter(counter-1);
            },1000);
        }
    },[counter])

    return (
        <Modal show={show} centered>
            <Modal.Header style={{background: '#FFA8A8'}}>
                <Modal.Title>Подтвердите действие</Modal.Title>
            </Modal.Header>
            <Modal.Body >
                <Alert variant="danger">Вы собираетесь полностью удалить остаток!</Alert>
                <p>Это удалит данный остаток из системы. Он более нигде не будет учитываться</p>
                <p>Если вы продали остаток - используйте кнопку "Продать"</p>
            </Modal.Body>
            <Modal.Footer >
                <Button variant="secondary" onClick={onCancel}>Отмена</Button>
                <Button variant="danger" onClick={onDelete} disabled={counter > 0}>
                    <span>{counter > 0 && <Badge bg="secondary">{counter}</Badge>} Удалить</span></Button>
            </Modal.Footer>
        </Modal>
    )

}