import { useEffect, useState } from "react";
import { Card, Col, Row, Table } from "react-bootstrap";
import NewRemainsElement from "./NewRemainsElement";


export default function({items, OnItemDelete, ...props}) {

    const [myItems,setMyItems] = useState([]);

    useEffect(() => {
    },[myItems]);

    useEffect(() => {
        setMyItems(items);
    },[items]);
    

    const NoRemainsDefined = () => {
        return (
            <Row className="text-center">
                <p className="text-muted" sm>Нет новых остатков. Добавьте их</p>
            </Row>
        );
    }

    return (
        <Col className="p-2">
                {myItems.length > 0 ?
                
                <Table hover>
                    <tbody>
                {myItems.map((item,index) => 
                    <NewRemainsElement key={index} item={item} onCancel={OnItemDelete} />
                )}
                    </tbody>
                </Table>
            :
                <NoRemainsDefined />}
        </Col>
    );
}