import { Button, Col, Container, Row } from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";
import HeaderNavBar from "../Components/Main/NavBar";

export default function ServiceDisabled({ showReturn, showMenu }) {
    const navigate = useNavigate();


    return (
        <>
            {showMenu && <HeaderNavBar />}
            <Container>
                <Row className="justify-content-md-center text-center p-3">
                    <Col md="auto"><Row><h1>Сервис отключен</h1></Row>
                        <Row>
                            <p>На данный момент данный сервис отключен.</p>
                        </Row>
                        {showReturn &&<Row>
                        <Col>
                            <Button variant="outline-secondary" onClick={() => navigate("/")}>Вернуться в главное меню</Button>
                            </Col>
                        </Row>}
                    </Col>
                    
                    </Row>

            </Container>
        </>
    );
}