import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Alert from 'react-bootstrap/Alert';
import { Col, Row, ButtonGroup} from 'react-bootstrap';
import { useState } from 'react';

function ShowAddRemainForm(props) {
    const [isLoading, setLoading] = useState(false);
    const [error, showError] = useState(false);

    const [sizeX, setX] = useState('');
    const [sizeY, setY] = useState('');
        
    async function SendNewRemain(e) {

        e.preventDefault()
        console.log(`Sending value with info ${sizeX} and ${sizeY}`);
        setLoading(true);
        setTimeout(() => {
            setLoading(false);
            showError(true);
        },3000);
    }


    return (
        <Form>
            <Row>
                <Col md>
                    <Form.Group className="mb-3" controlId='fromSizeX'>
                        <Form.Label>Ширина рулона:</Form.Label>
                        <Form.Control type="number" disabled={isLoading} placeholder='Ширина рулона в метрах' value={sizeX} onChange={(e) => setX(e.target.value)}></Form.Control>
                    </Form.Group>
                </Col>
                <Col md>
                    <Form.Group className="mb-3" controlId='fromSizeY'>
                        <Form.Label>Длина рулона:</Form.Label>
                        <Form.Control type="number" disabled={isLoading} placeholder='Длина рулона в метрах' value={sizeY} onChange={(e) => setY(e.target.value)}></Form.Control>
                    </Form.Group>
                </Col>
            </Row>
            <>
                <Alert show={error} variant='danger'>
                    <p>Произошла неизвестная ошибка. Попробуйте позже</p>
                </Alert>
            </>
            <ButtonGroup className="float-end">
                <Button variant="primary" disabled={isLoading} onClick={SendNewRemain}>Зарегистрировать остаток</Button>
                <Button variant="secondary">Отмена</Button>
            </ButtonGroup>
        </Form>
    );
}

export default ShowAddRemainForm;