import React, { useEffect, useState } from "react";
import { Button, ButtonGroup, Card, Col, Container, Modal, Row } from "react-bootstrap";
import NewRemainsList from "../Components/NewRemainsList";
import NewRemainModal from "../Components/NewRemainModal";
import Html5QrcodePlugin from "../../../Components/BarcodeScanner/BarcodeScanner";
import axios from "axios";
import Config from '../../../config.json';
import AddFinishedModal from "../Components/AddFinishedModal";



export default function(props) {

    const removeNewItem = (index) => {
        let newRemainsEdited = [...newRemains];
        newRemainsEdited.splice(index,1);
        setNewRemains(newRemainsEdited);
    }

    const [newRemains,setNewRemains] = useState([]);
    const [addingInProgress,setAddProgress] = useState(false);
    const [showFinish,setFinish] = useState(false);
    const [addResult,setAddResult] = useState({});
    const [newRemainModalShow, setModalShow] = useState(false);
    const [newRemainModalData, setModalData] = useState({
        needLoad: false,
        data: {}
    });

    const removeAddedRemains = (result) => {
        setNewRemains(result.failedItems);
    }

    const [openScanner, setOpenScanner] = useState(false);
    
    const onAddNewItems = () => {
        setAddProgress(true);
        axios.post(
            `${Config.API_URL}/remains/add`,
            {items: newRemains}
        ).then(data => {
            console.log(data.data)
            setAddResult(data.data);
            setFinish(true);
            removeAddedRemains(data.data);
        }).catch(e => {
            console.log(e);
        }).finally(() => {
            setAddProgress(false);
        });
    }

    const onModalSuccess = (item) => {
        let newRemainsEdited = [item, ...newRemains];
        setNewRemains(newRemainsEdited);
        setModalShow(false);
    }

    const addNewModal = () => {
        setModalData({
            needLoad: false,
            data: {}
        });
        setModalShow(true);
    }

    const OnScanSuccess = (foundedItem) => {
        let newModalData = {
            needLoad: true,
            data: foundedItem
        }
        setModalData(newModalData);
        setOpenScanner(false);
        setModalShow(true);
    }

    useEffect(() => {
    },[]);

    return (
        <>
            <AddFinishedModal show={showFinish} data={addResult} onClose={() => setFinish(false)} />

            <Container className="p-2">
                <Row>
                    <Col className="text-center">
                        <h4>Добавление новых остатков</h4>
                    </Col>
                </Row>

                <Row>
                    <Col>
                        <h6>Список остатков</h6>
                        
                    </Col>
                </Row>
                <Row>
                    <NewRemainsList items={newRemains} OnItemDelete={removeNewItem} />
                </Row>
                <Row>
                    <ButtonGroup>
                        <Button variant="outline-secondary" onClick={addNewModal} disabled={addingInProgress}>Добавить новый</Button>
                        <Button variant="outline-success" onClick={onAddNewItems} disabled={newRemains.length == 0 || addingInProgress}>
                            Завершить
                        </Button>
                    </ButtonGroup>
                </Row>
                <Row>
                    <ButtonGroup>
                        <Button variant="outline-secondary" disabled={addingInProgress} onClick={() => setOpenScanner(true)}>
                            Открыть сканнер
                        </Button>
                    </ButtonGroup>
                </Row>
            </Container>

            <NewRemainModal show={newRemainModalShow} onCancel={() => setModalShow(false)} onSubmit={onModalSuccess} AutoloadData={newRemainModalData}/>

            <Modal show={openScanner}>
                <Modal.Title>
                    <Modal.Header>Сканнер штрихкода</Modal.Header>
                    <Modal.Body>
                        {openScanner && <Html5QrcodePlugin onScannedComplete={OnScanSuccess} />}
                    </Modal.Body>
                </Modal.Title>
                <Modal.Footer>
                    <Row>
                        <Button variant="outline-secondary" onClick={() => setOpenScanner(false)}>Отмена</Button>
                    </Row>
                </Modal.Footer>
            </Modal>

            
        </>
    );
}