import { Button, Card, Col, Container, Row } from "react-bootstrap";
import HeaderNavBar from "../Components/Main/NavBar";
import { Link } from "react-router-dom";


export default function(props) {


    return (
        <>
            <HeaderNavBar usetData={props.User}/>
            <Container>
                    <Row className="p-1">
                        <Col md={3}>
                            <Link to="/"><Button variant="outline-secondary">
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-return-left" viewBox="0 0 16 16">
                                <path fill-rule="evenodd" d="M14.5 1.5a.5.5 0 0 1 .5.5v4.8a2.5 2.5 0 0 1-2.5 2.5H2.707l3.347 3.346a.5.5 0 0 1-.708.708l-4.2-4.2a.5.5 0 0 1 0-.708l4-4a.5.5 0 1 1 .708.708L2.707 8.3H12.5A1.5 1.5 0 0 0 14 6.8V2a.5.5 0 0 1 .5-.5"/>
                            </svg>
                            </Button></Link>
                        </Col>
                        <Col>
                        </Col>
                    </Row>
                
                <Card>
                    <Container className="p-2">
                        <Row>
                            <Col>
                                <h3>О веб-приложении</h3>
                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                <p>Веб-приложение разработано для автоматизации рутинных операций в отделе "Напольные покрытия"</p>
                                <p></p>
                            </Col>
                        </Row>
                        <hr/>
                        <Row>
                            <Col className="text-muted">
                                <p>Данное веб-приложение не использует никакие чувствительные данные ООО "ЛЕ МОНЛИД". Вся используемая информация находится в свободном доступе.</p>
                                <p> 
                                     Не рекомендуется использовать такие данные как LDAP, пароли, которые используются в локальной сети магазина
                                </p>
                            </Col>
                        </Row>
                    </Container>
                </Card>

            </Container>
        </>
    );
}