import { Card, Image, Row } from "react-bootstrap";


function MenuButton({onClick, src, Label, ...props}) {

    return (
        <Card onClick={onClick} className="shadow">
            <Card.Body>
                <Row ><Image src={src} className="d-flex justify-content-center " rounded/></Row>
                <Row xs={0} md className="d-flex justify-content-center text-center pt-2">{Label}</Row>
            </Card.Body>
        </Card>
    )
}

export default MenuButton;