import { Col, Container, Row } from "react-bootstrap";
import { Link } from "react-router-dom";

export default function ErrorPage({ showReturn }) {
    return (
        <>
            <Container>
                <Row className="justify-content-md-center">
                    <Col md="auto"><Row><h1>Произошла ошибка</h1></Row>
                    <Row><p>Страница не найдена. 
                        {showReturn && <>Попробуйте вернуться <Link to="/">назад</Link></> }</p></Row></Col>
                </Row>
            </Container>
        </>
    );
}