import { useEffect, useRef, useState } from "react";
import { Accordion, Badge, Button, ButtonGroup, Card, Col, Container, Form, Nav, Navbar, Row, Spinner } from "react-bootstrap";
import RemainsInfo from "../Components/RemainsInfo";
import axios from "axios";
import Config from "../../../config.json"
import RemoveRemainConfirmation from "../Components/RemoveRemainConfirmation";
import PrintOneRemain from "../Components/PrintOneRemainModal";


export default function() {
    
    const [remains,setRemains] = useState({data: []});
    const [filteredRemains,setFRemains] = useState({data: []});
    const [dataLoaded, setDataLoaded] = useState(false);
    const [needUpdate, setUpdate] = useState(false);
    
    const [printShow,setPrintShow] = useState(false);
    const [deleteShow,setDeleteModal] = useState(false);    
    const [selectedRemain,setSelected] = useState(0);
    const [selectedRemainItem,setSelectedItem] = useState(null);

    const [searchText, setSearchText] = useState("");

    const removeItemSize = function(itemID) {
        let items = {data:[...remains.data]};
        console.log("OLD STATE",remains);
        items.data.forEach(item => {
            let index = -1;
            item.List.forEach(data => {
                index += 1;
                if(data?.id == itemID) {
                    item.List.splice(index,1);
                    setRemains(prevState => {
                        const ret = items;
                        return items;
                    });
                }
            });
        });

        axios.get(
            `${Config.API_URL}/remains/remove?id=${selectedRemain}`).then(() => {console.log("OK")}).catch((e) => {console.log(e)})
    }

    const openRemoveDialog = function(itemID) {
        console.log(itemID);
        setSelected(itemID);
        setDeleteModal(true);
    }

    const approveDelete = function() {
        removeItemSize(selectedRemain);
        setDeleteModal(false);
    }

    useEffect(() => {
        setFRemains(remains);
    },[remains]);

    useEffect(() => {

        axios.get(
            `${Config.API_URL}/remains/get?unprinted=0`
        ).then(data => {
            console.log(data);
            for(let item of data.data) {
                let S = 0;
                for(let element of item.List) {
                    S += element.sizeX * element.sizeY;
                }
                item.totalS = S;
            }
            setRemains(data);
            setFRemains(data);
            setDataLoaded(true);
        }).catch(() => {
            setDataLoaded(false);
        });
        
        return () => {
            setDataLoaded(false);
        }
    },[]);

    const Search = () => {
        if(searchText.length > 0) {
            var filtered = remains.data.filter(x => String(x.Name).includes(searchText));
        }
        else {
            var filtered = remains.data;
        }
        setFRemains({data: filtered});
    }

    const printRemain = (Remain,index) => {
        console.log("REMAIN: ",Remain);
        console.log("INDEX: ",index);
        setPrintShow(true);
        setSelected(index);
        setSelectedItem(Remain);
    }

    const LoadingDataAnimation = (props) => {
        return (
            <>
                <Container fluid className="d-flex justify-content-center text-center">
                    <Row>
                        <Col md="auto" className="p-5">
                            <Spinner animation="border" role="status" size="lg">
                                
                            </Spinner>
                            <h3>Загрузка...</h3>
                        </Col>
                    </Row>
                </Container>
            </>
        )
    };


    const NoDataInformation = (props) => {
        return (
            <>
                <Container fluid className="d-flex justify-content-center text-center">
                    <Row>
                        <Col md="auto" className="p-5">
                            <h3>Остатков не осталось.</h3>
                        </Col>
                    </Row>
                </Container>
            </>
        )
    };

    const sellRemain = async (remainID) => {
        let result = axios.get(
            `${Config.API_URL}/remains/sell?id=${remainID}`
        );
        if(result.status == 200) {
            let items = {data:[...remains.data]};
            console.log("OLD STATE",remains);
            items.data.forEach(item => {
                let index = -1;
                item.List.forEach(data => {
                    index += 1;
                    if(data?.id == remainID) {
                        item.List.splice(index,1);
                        setRemains(prevState => {
                            const ret = items;
                            return items;
                        });
                    }
                });
            });
        }
    }

    const RemainsDataRender = ({data, ...props}) => {
        

        return (
            <>
                
                <RemoveRemainConfirmation show={deleteShow} remain={selectedRemain} onCancel={() => {setDeleteModal(false)}} onDelete={approveDelete} />
                <PrintOneRemain show={printShow} Remain={selectedRemainItem} index={selectedRemain} onCancel={() => setPrintShow(false)} onPrint={() => setPrintShow(false)} />
                <Container>
                    <Row>
                        <Col className="p-1" style={{
                            maxHeight: '263px',
                            overflowY: 'scroll'
                        }}>
                        { filteredRemains.data.length > 0 ?
                        <Accordion defaultActiveKey="0">
                            {filteredRemains.data.map(item => 
                                <Accordion.Item eventKey={item.lm_code} key={item.lm_code}>
                                    <Accordion.Header>
                                        <Row>
                                            <Col>
                                                <span><Badge className="p-2 float-left">{item.List.length}</Badge> {item.Name}</span>
                                            </Col>
                                        </Row>
                                    </Accordion.Header>
                                    <Accordion.Body>
                                        <Card className="mb-2 p-2">
                                            <Card.Text>
                                                <Row>
                                                    <Col>
                                                        Общая площадь:
                                                    </Col>
                                                    <Col className="d-flex justify-content-end">
                                                        <Badge bg="secondary">{item.totalS.toFixed(2)} m2</Badge>
                                                    </Col>
                                                </Row>
                                            </Card.Text>
                                        </Card>
                                        {item.List.map((remain,index) => 
                                            <RemainsInfo RemainItem={remain} RemainMain={item} OnDeleteClick={openRemoveDialog} index={index} OnSellClick={sellRemain} OnPrintClick={printRemain} targetID={remain.id} key={remain.id}/>
                                        )}
                                    </Accordion.Body>
                                </Accordion.Item>
                                
                            )}
                        </Accordion>
                        : <NoDataInformation />}
                        </Col>
                    </Row>
                </Container>
            </>
        )
    };

    return (
        <>
        <Container className="p-2 ">
            
            {/*  */}
            <Row>
                <Col className="text-center">
                    <h4>Список остатков</h4>
                </Col>
            </Row>
            <Row>
                <Col>
                    <Card>
                        {!dataLoaded ? <LoadingDataAnimation /> : <RemainsDataRender data={remains}/>}
                    </Card>
                </Col>
            </Row>
            <Row className="pt-2">
                <Col>
                    <Card>
                        <Card.Text className="p-2">
                            <p>Легенда обозначений:</p>
                            <p><Badge bg="success">Новый</Badge> - остаток был добавлен менее 2-х дней назад.</p>
                            <p><Badge bg="danger">Неактуален </Badge> - Остаток не обновлялся в течении 7 дней. Требуется подтверждение, что он еще не был продан</p>
                            <p><Badge bg="warning">Не распечатан</Badge> - Остаток еще не был распечатан системой</p>
                        </Card.Text>
                    </Card>
                </Col>
            </Row>
        </Container>
        </>
    )
}