import { useEffect, useState } from "react";
import { Badge, Button, ButtonGroup, Card, Col, Container, Row } from "react-bootstrap";

function CalculateDays(targetDate) {
    let Now = new Date();
    let Target = new Date(targetDate);
    const oneDay = 1000 * 60 * 60 * 24;
    const diffInTime = Now.getTime() - Target.getTime();
    const diffInDays = Math.round(diffInTime / oneDay);
    return diffInDays;
}

export default function({RemainItem,RemainMain,index, OnDeleteClick,targetID, OnSellClick, OnPrintClick, ...props}) {

    const [remain,setRemain] = useState(RemainItem);
    const [selled,setSelled] = useState(false);

    const NewBadge = () => {
        return (
            <Badge bg="success">Новый </Badge>
        )
    }

    const NeedConfirmation = () => {
        return (
            <Badge bg="danger">Неактуален </Badge>
        )
    }

    const NeedPrint = () => {
        return (
            <Badge bg="warning">Не распечатан</Badge>
        )
    }

    useEffect(() => {
        setRemain(RemainItem);
    },[RemainItem])

    const onSellClick = () => {
        setSelled(true);
        OnSellClick(remain.id);
    }

    return (
        <>
        <Card className="mb-1 d-none d-sm-block shadow"> {/*desktop*/}
            <Container fluid className="p-2">
                <Row id={`item-status-${RemainItem.id}`}>
                    <Col>
                        <p>
                            <span> </span>
                        {CalculateDays(RemainItem.created) <= 2 && <NewBadge />}
                        {CalculateDays(RemainItem.confirmedDate) >= 7 && <NeedConfirmation />}
                        {!RemainItem.Printed && <NeedPrint />}
                        </p>
                    </Col>
                    <Col className="d-flex justify-content-end">
                        <p><span className="text-muted">Добавлено: </span> {(new Date(RemainItem.created)).toLocaleDateString()} {(new Date(RemainItem.created)).toLocaleTimeString()}</p>
                    </Col>
                </Row>
                <Row className="m-2">
                    <ButtonGroup >
                            {!selled ? 
                            <Button variant="outline-success" onClick={() => onSellClick()}>Продать</Button>
                            :
                            <Button variant="success" disabled>ПРОДАН</Button>
                            }
                            { OnPrintClick && <Button variant="outline-secondary" disabled={selled} onClick={() => OnPrintClick(RemainMain,index)}>Печать</Button> }
                            { OnDeleteClick &&<Button variant="outline-danger" disabled={selled}  onClick={() => OnDeleteClick(remain.id)}>Удалить</Button>}
                    </ButtonGroup>
                </Row>
                <Row>
                    <Col>
                        <Row>
                            <Col>
                                Площадь:
                            </Col>
                            <Col >
                                <Badge bg="secondary" className="float-right">{(remain.sizeX*remain.sizeY).toFixed(2)}</Badge> м2
                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                Ширина:
                            </Col>
                            <Col >
                                <Badge bg="secondary" className="float-right">{remain.sizeX.toFixed(2)}</Badge> м
                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                Длина:
                            </Col>
                            <Col >
                                <Badge bg="secondary" className="float-right">{remain.sizeY.toFixed(2)}</Badge> м
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </Container>
        </Card>

        <Card className="mb-1 d-block d-sm-none shadow-sm"> {/*mobile*/}
        <Container fluid className="p-2">
            <Row id={`item-status-${RemainItem.id}`}>
                <Col>
                    <p>
                    {CalculateDays(RemainItem.created) <= 2 && <NewBadge />}
                    {CalculateDays(RemainItem.confirmedDate) >= 7 && <NeedConfirmation />}
                       {!RemainItem.Printed && <NeedPrint />}
                    </p>
                </Col>
                <Col className="d-flex justify-content-end">
                    <p><span className="text-muted">{(new Date(RemainItem.created)).toLocaleDateString()}</span></p>
                </Col>
            </Row>
            <Row>
                <Col>
                <Row>
                            <Col>
                                Площадь:
                            </Col>
                            <Col >
                                <Badge bg="secondary" className="float-end">{(remain.sizeX*remain.sizeY).toFixed(2)} м2</Badge>
                            </Col>
                        </Row>
                    <Row>
                        
                        <Col>
                            Ширина:
                        </Col>
                        <Col >
                            <Badge bg="secondary" className="float-end">{remain.sizeX.toFixed(2)} м</Badge>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            Длина:
                        </Col>
                        <Col >
                            <Badge bg="secondary" className="float-end">{remain.sizeY.toFixed(2)} м</Badge>
                        </Col>
                    </Row>
                </Col>
                
            </Row>
                <Row className="m-1">
                    {!selled ? 
                    <Button variant="outline-success" onClick={() => onSellClick()}>Продать</Button>
                    :
                    <Button variant="success" disabled>ПРОДАН</Button>
                    }
                </Row>
                <Row className="m-1">
                    <Button variant="outline-danger" disabled={selled} onClick={() => OnDeleteClick(remain.id)}>Удалить</Button>
                </Row>
        </Container>
        </Card>
        </>
    )
}