import axios from "axios";
import { useEffect, useState } from "react";
import { Alert, Button, Col, Container, Form, FormGroup, Modal, Row, Spinner } from "react-bootstrap";
import Config from "../../../config.json"


export default function PrintOneRemain({Remain, index, show, onPrint, onCancel, ...props}) {
    const [price,setPrice] = useState('');
    const [isAllowed,setAllowed] = useState(false);
    const [printing,setPrinting] = useState(false);

    const isAllowedNext = () => {
        setAllowed(price.toString().length > 0 && Number(price) > 0)
    }

    const printRemain = () => {
        let remain = [{
            lm_code: Remain.lm_code,
            Name: Remain.Name,
            price: price,
            List: [
                Remain.List[index]
            ]
        }];
        console.log("REMAIN 22",remain);
        setPrinting(true);
        axios.post(
            `${Config.API_URL}/remains/printRemains`,
            {items: remain},{
                responseType: 'blob'
            }
        ).then((response) => {
            const file = new Blob([response.data],{type: "application/pdf"});
            const fileURL = URL.createObjectURL(file);
            const pdfWindow = window.open();
            pdfWindow.location.href = fileURL;
        }).catch((err) => {
            alert(err);
        }).finally(() => {
            onPrint();
        });
    }

    useEffect(() => {
        console.log("SHOW",show);
    },[Remain]);

    return (
        <Modal show={show} centered>
            <Modal.Header>
                <Modal.Title>
                    Печать остатка
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                { !printing ? <>
                <Alert variant="warning">Укажите цену за остаток</Alert>
                <FormGroup>
                    <Form.Label>Цена за м2:</Form.Label>
                    <Form.Control type="number" placeholder="" value={price} onChange={(e) => setPrice(e.target.value)}></Form.Control>
                </FormGroup>
                </> : 
                    <Container fluid className="d-flex justify-content-center text-center">
                    <Row>
                        <Col md="auto" className="p-5">
                            <Spinner animation="border" role="status" size="lg">
                            </Spinner>
                            <h3>Формируем остаток...</h3>
                        </Col>
                    </Row>
                    </Container>
                }
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={onCancel}>Отмена</Button>
                <Button variant="primary" disabled={(isAllowed || printing)} onClick={() => printRemain()}>Печать</Button>
            </Modal.Footer>
        </Modal>
    )

}