import axios from 'axios';
import { Html5Qrcode, Html5QrcodeScanner } from 'html5-qrcode';
import { useEffect, useState } from 'react';
import Config from "../../config.json";
import { Alert, Card, Container } from 'react-bootstrap';

const qrcodeRegionId = "html5qr-code-full-region";

// Creates the configuration object for Html5QrcodeScanner.
const createConfig = (props) => {
    let config = {};
    if (props.fps) {
        config.fps = props.fps;
    }
    if (props.qrbox) {
        config.qrbox = {
            width: 250,
            height: 250
        };
    }
    if (props.aspectRatio) {
        config.aspectRatio = props.aspectRatio;
    }
    if (props.disableFlip !== undefined) {
        config.disableFlip = props.disableFlip;
    }
    return config;
};



const Html5QrcodePlugin = ({onScannedComplete, ...props}) => {

    const [alertData,setAlert] = useState({
        show: false,
        variant: "success",
        message: ""
    });

    const OnSuccess = (decodedText,result) => {
        axios.get(
            `${Config.API_URL}/items/get?lm=${decodedText}`
        ).then((result) => {
            if(result.status == 200) {
                setAlert({
                    show: true,
                    variant: "success",
                    message: "Сканирование успешно"
                });
                onScannedComplete(result.data);
            }
            else {
                setAlert({
                    show: true,
                    variant: "warning",
                    message: "LM код не найден"
                });
            }
        }).catch((e) => {
            if(e.status == 404) {
                console.log("Not found")
            }
            else {
                console.log("Internal Error")
            }
            setAlert({
                show: true,
                variant: "danger",
                message: "Не удается отсканировать"
            });
        });
    }

    const verbose = props.verbose === true;
    const config = createConfig(props);
    let html5QrcodeScanner = null;
    var scanning = false;

    const [ready,setReady] = useState(false);
    const [notAllowed,setNotAllowed] = useState(false);

    useEffect(() => {
        try {
        console.log("Mounted");
        // when component mounts
        if(html5QrcodeScanner == null) {
            html5QrcodeScanner =new Html5Qrcode(qrcodeRegionId);
            Html5Qrcode.getCameras().then(devices => {
                if (devices && devices.length) {
                  var cameraId = devices[1].id;
                  scanning = true;
                  html5QrcodeScanner.start(cameraId, config, OnSuccess, (e) => {});
                  setReady(true);
                }
                else {
                    setNotAllowed(true);
                }
              }).catch(err => {
                setNotAllowed(true);
              });
            
        }
        }
        catch(e) {
            setNotAllowed(true);
        }
        // cleanup function when component will unmount
        return () => {
            if(scanning) {
                console.log("Unmounted");
                html5QrcodeScanner.stop().catch(error => {
                    console.error("Failed to clear html5QrcodeScanner. ", error);
                });
                scanning = false;
                html5QrcodeScanner = null;
            }
        };
    }, []);

    return (
        <Container>
            { !notAllowed ? <>
            <div id={qrcodeRegionId}></div>
            
            { ready &&
            <div style={{
                width: 250,
                height: 250,
                background: 'rgba(0,0,0,0.2)',
                borderStyle: '25px solid',
                borderColor: 'rgba(1,1,255,0.8)',
                position: 'absolute', 
                top: '50%', 
                left: '50%', 
                transform: 'translate(-50%, -50%)'
            }} /> }
            <Alert className='' style={{ 
                position: 'absolute', 
                top: '80%', 
                left: '50%', 
                transform: 'translate(-50%, -20%)', 
                }}  show={alertData.show} variant={alertData.variant}>{alertData.message}
            </Alert>
            </> :
            <Alert show={true} variant="danger">Нельзя запустить сканнер: сканнер доступен только для мобильной версии</Alert>}
        </Container>
    );
};

export default Html5QrcodePlugin;