import { useState } from "react";
import { Alert, Button, Card, Col, Container, Form, InputGroup, Modal, Row, Spinner } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import Cookies from 'universal-cookie';
import axios from 'axios';
import Config from '../config.json';

export default function AuthPage(props) {

    const cookies = new Cookies();
    const navigate = useNavigate();

    const LoadingComponent = () => {
        return (
            <Container>
                <h4>Идет процесс авторизации</h4>
                <Spinner animation="border"/>
            </Container>
        );
    }


    const [userPhone,setUserPhone] = useState("");
    const [modalPhoneShow,setModalShow] = useState(false);
    const [allowedNext,setAllowed] = useState(false);
    const [isLoading,setLoading] = useState(false);
    const [error,setError] = useState({
        error: false,
        message: ""
    });
    const [authState,setAuthState] = useState(0);

    const [authCode,setAuthCode] = useState("");

    const normalizePhone = (value,previousValue) => {
        if (!value) return value;
        const currentValue = value.replace(/[^\d]/g, '');
        const cvLength = currentValue.length;
        
        if (!previousValue || value.length > previousValue.length) {
            if (cvLength < 4) return currentValue;
            if (cvLength < 6) return `(${currentValue.slice(0, 3)}) ${currentValue.slice(3)}`;
            if(cvLength >= 10) setAllowed(true) 
                else setAllowed(false);
            return `(${currentValue.slice(0, 3)}) ${currentValue.slice(3, 6)}-${currentValue.slice(6, 8)}-${currentValue.slice(8, 10)}`;
        }
    }

    const login = () => {
        setLoading(true);
        axios.post(
            `${Config.API_URL}/auth/request`,
            {
                phone: userPhone
            }).then(response => {
                if(response.status === 200) {
                console.log(response);
                setLoading(false);
                if(response.data.code == "WAITING_AUTH_CODE") {
                    setAuthState(1);
                }
                }
                else {
                    setLoading(false);
                    setError({
                        error:true,
                        message:"Пользователь не найден"
                    });
                }
            }).catch(error => {
                console.log(error);
                setLoading(false);
                console.log(error);
                if(error.response.data) {
                    setError({
                        error:true,
                        message:"Пользователь не найден"
                    });
                }
            });
    }

    const debugLoginSucces = (e) => {
        setLoading(true);
        setTimeout(() => {
            console.log("Loggen in");
            cookies.set('logged0','true');
            navigate("/");
        },3000);
    }

    const changePhoneHandle = ({target: {value}}) => {
        setUserPhone(normalizePhone(value));
    }

    const finishAuth = (e) => {
        e.preventDefault();
        axios.post(
            `${Config.API_URL}/auth/verifyAuth`,
            {
                code: authCode
            }).then(({ data }) => {
                console.log(data);
                if(data.ok) {
                    cookies.set("Autorization",`Bearer ${data.data}`);
                    navigate("/");
                }
                else {
                    setError({
                        error: true,
                        message: "Неверный код подтверждения"
                    });
                }
            }).catch((e) => {
                setError({
                    error: true,
                    message: "Произошла неизвестная ошибка"
                });
            });
    }

    return (
        <>
            <Container className="text-center justify-content-center align-items-center">
                <Row>
                    <Col>
                        <h1>Вы не авторизированы в приложении</h1>
                    </Col>
                </Row>
                <Row>
                    <Col >
                        <p>Для продолжения требуется авторизация в приложении</p>
                        
                        <Card className="d-inline-flex p-2">
                            {!isLoading ?
                            <Container className="p-2">
                            <Col>
                            {authState == 0 ? 
                        <Form>
                            <Form.Group md="auto">
                                <Form.Label>Номер телефона:</Form.Label>
                                <InputGroup>
                                    <InputGroup.Text>+7</InputGroup.Text>
                                    <Form.Control placeholder="(999)-999-99-99" value={userPhone} onChange={changePhoneHandle}/>
                                </InputGroup>
                                <Form.Text muted>
                                    <p className="interacteble" onClick={() => setModalShow(true)}>Зачем это нужно?</p>
                                </Form.Text>
                            </Form.Group>
                            <Alert show={error.error} variant="danger" className="small">{error.message}</Alert>
                            <Button type="submit" variant="outline-secondary" disabled={!allowedNext}
                            onClick={login}>
                                { allowedNext ? "Авторизироваться" : "Введите номер телефона" }
                            </Button>
                        </Form>
                        :
                        <Form className="">
                            <Form.Group md="1" className="p-2">
                                <Form.Label>Код подтверждения:</Form.Label>
                                <InputGroup>
                                    <Form.Control placeholder="Введите полученный код авторизации" value={authCode} onChange={(e) => {setAuthCode(e.target.value)}}/>
                                </InputGroup>
                            </Form.Group>
                            <Alert show={error.error} variant="danger" className="small">{error.message}</Alert>
                            <Button type="submit" variant="outline-secondary" disabled={!allowedNext}
                            onClick={finishAuth}>
                                Подтвердить
                            </Button>
                        </Form>
                        }
                    </Col>
        </Container>
                            : <LoadingComponent/> }
                        </Card>
                    </Col>
                </Row>
            </Container>



            <Modal
                size="md"
                centered
                show={modalPhoneShow}
            >
                <Modal.Body>
                    <h5>Зачем нужен номер телефона?</h5>
                    <p>Номер телефона требуется для ограничения доступа к функциям приложения извне.</p>
                    <h5>Почему не использовать LDAP?</h5>
                    <p>LDAP является чувствительной информацией</p>
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={() => setModalShow(false)} variant="primary">ОК</Button>
                </Modal.Footer>
            </Modal>
        </>
    );
}