

/*
    LM
    Name
    sizeX
    sizeY
*/

import { useEffect } from "react";
import { Badge, Button, Col, Row } from "react-bootstrap";

export default function({item, onCancel, ...props}) {
    

    return (            
            <tr>
                <td>
                    {item.Name}
                </td>
                <td>
                    <Badge bg="primary">{item.sizeX}x{item.sizeY}</Badge>
                </td>
                <td>
                    <Button variant="outline-danger" className="float-end" onClick={() => onCancel(props.key)}>Отмена</Button>
                </td>
            </tr>
    );
}