import axios from "axios";
import { useEffect, useState } from "react";
import { Button, Card, Col, Container, Modal, Row, Spinner } from "react-bootstrap";
import Config from "../../../config.json"
import UnprintedRemain from "../Components/UnprintedRemain";
const download = require('downloadjs');


export default function() {
    
    const [unprintedRemains,setRemains] = useState([]);
    const [loading,setLoading] = useState(false);
    const [ready,setReady] = useState(false);
    const [success,setPrintingSuccess] = useState(false);
    
    const HandleItemChange = (index,price) => {
        console.log(index,price);
        var remains = [...unprintedRemains];
        remains[index].price = price;
        setRemains(remains);
    }

    const ReadyToPrint = () => {
        let ready = unprintedRemains.length > 0;
        for(let item of unprintedRemains) {
            if(item.price <= 0 || item.price == "" || item.price == undefined) {
                ready = false;
            }
        }
        return ready;
    }

    useEffect(() => {
        let ready = ReadyToPrint();
        setReady(ready);
    },[unprintedRemains])


    useEffect(() => {
        setLoading(true);

        axios.get(
            `${Config.API_URL}/remains/get?unprinted=1`
        ).then(data => {
            
            for(var item of data.data) {
                item.price = 0;
            }
            console.log(data);
            setRemains(data.data);
        }).catch((e) => {
            console.log(e);
        }).finally(() => {
            setLoading(false);
        });
        
        return () => {
            setLoading(false);
        }
    },[]);

    const OnPrintingPressed = () => {
        setLoading(true);
        axios.post(
            `${Config.API_URL}/remains/printRemains`,
            {items: unprintedRemains},{
                responseType: 'blob'
            }
        ).then((response) => {
            const file = new Blob([response.data],{type: "application/pdf"});
            const fileURL = URL.createObjectURL(file);
            const pdfWindow = window.open();
            pdfWindow.location.href = fileURL;
            setPrintingSuccess(true);
        }).catch((err) => {
            
        }).finally(() => {
            setLoading(false);
        });
    }

    const OnDeleteUnprinted = () => {
        setPrintingSuccess(false);
        setLoading(true);
        const pinters = [...unprintedRemains];
        let values = [];
        console.log(pinters);
        for(const items of pinters) {
            console.log(items);
            values = values.concat(items.List);
            console.log(values);
        }
        
        axios.post(
            `${Config.API_URL}/remains/setPrinted`,{
                data: values
            }).then((responce) => {
                if(responce.status == 200) {
                    setRemains([]);
                }
                else {
                    
                }
            }).catch((reason) => {

            }).finally(() => {
                setLoading(false);
            });
        
    }

    const LoadingDataAnimation = (props) => {
        return (
            <>
                <Container fluid className="d-flex justify-content-center text-center">
                    <Row>
                        <Col md="auto" className="p-5">
                            <Spinner animation="border" role="status" size="lg">
                                
                            </Spinner>
                            <h3>Загрузка...</h3>
                        </Col>
                    </Row>
                </Container>
            </>
        )
    };

    const NoData = (props) => {
        return (
            <>
                <Container fluid className="d-flex justify-content-center text-center">
                    <Row>
                        <Col md="auto" className="p-5">
                            <h3>Не распечатанных остатков нет</h3>
                        </Col>
                    </Row>
                </Container>
            </>
        )
    };


    return (
        <>
        <Modal show={success} centered>
            <Modal.Header>
                <Modal.Title>Остатки сформированы</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <p>Остатки успешно сформированы.</p>
                <p>Отметить текущие остатки как "Распечатанные"?</p>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={() => {setPrintingSuccess(false)}}>Нет</Button>
                <Button variant="success" onClick={OnDeleteUnprinted}>Да</Button>
            </Modal.Footer>
        </Modal>
        <Container >
            <Row className="p-2">
                <Col>
                    <h3>Печать остатков</h3>
                    <Card>
                        <Card.Text >
                            <Container fluid>
                                <Row>
                                    <Col className="p-2">
                                        <p>Ниже представлены остатки, которые были созданы, но не распечатаны.</p>
                                        <p>Не забудьте указать цену</p>
                                    </Col>
                                </Row>
                            </Container>
                        </Card.Text>
                    </Card>
                </Col>
            </Row>
            <Row className="p-2">
                <Col>
                    <Card className="p-2">
                        <Container fluid>
                            <Row className="pb-2"><Button onClick={OnPrintingPressed} variant="success" disabled={!ready  || loading}>
                                {ready ? "Сформировать" : 
                                 unprintedRemains.length <= 0 ? "Нет остатков" : "Указаны не все цены"
                                }
                            </Button></Row>
                            <Row>
                                <Col>
                                <Card className="p-2" id="unprintedList" style={{maxHeight: '300px', overflowY: 'scroll'}}>
                                    { loading ? <LoadingDataAnimation /> : 
                                        <Row>
                                            
                                            <Col>
                                                {unprintedRemains.length > 0 ? 
                                                    unprintedRemains.map((value,index) => {
                                                        console.log(index);
                                                        return <UnprintedRemain Item={value} arrNum={index} onPriceChanged={HandleItemChange}/>
                                                    })
                                                : <NoData />}
                                            </Col>
                                        </Row>
                                    }
                                </Card>
                                </Col>
                            </Row>
                        </Container>
                        
                    </Card>
                </Col>
            </Row>
        </Container>
        </>
    )
}