import { Button, ButtonGroup, Card, Col, Container, Dropdown, DropdownButton, DropdownItem, Row, SplitButton } from "react-bootstrap";
import { useNavigate } from "react-router-dom";

export default function() {
    const navigate = useNavigate();

    const moveToList = () => {navigate("/remains/list")}
    const moveToAnalytics = () => {navigate("/remains/addRemains")}
    const moveToPrint = () => {navigate("/remains/preparePrinting")}


    return (
    <Card>
        <Container className="p-2">
            <Row className="p-2">
                <Col>
                <h3>Быстрый доступ</h3>
                <ButtonGroup aria-label="Basic example">
                    <Button variant="outline-secondary" onClick={moveToList}>Список остатков</Button>
                    <Button variant="outline-secondary" onClick={moveToAnalytics}>Добавить остатки</Button>
                    <Button variant="outline-secondary" onClick={moveToPrint}>Пакетная печать</Button>
                </ButtonGroup>
                </Col>
            </Row>
        </Container>
    </Card>
    );
}